import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import IHomeLoginData from "./models/IHomeLoginData";

@Component({
  selector: 'lib-home-login',
  templateUrl: './home-login.component.html',
  styleUrls: ['./home-login.component.sass']
})
export class HomeLoginComponent implements OnInit {

  @Input() data: IHomeLoginData;
  @Output() navigateTo = new EventEmitter();
  public loginForm: FormGroup;

  constructor(
    private _formBuilder: FormBuilder
    ) {
  }

  ngOnInit() {
   
    this.loginForm = this._formBuilder.group({
      user: ['', [Validators.required, Validators.minLength(5)]],
      password: ['', Validators.required],
      accessOn: new Date().toISOString().substring(0, 10)
    });

  }

  /**Handle form in view*/
  public get lf() {
    return this.loginForm.controls;
  }


}
